import React, { useEffect, useState } from "react";

interface Bridge {
  structureNumber: string;
}

const Sidebar: React.FC = () => {
  const [bridges, setBridges] = useState<Bridge[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchBridges = async () => {
      try {
        const res = await fetch("/api/getbridges");
        const data = await res.json();

        if (!res.ok) {
          throw new Error(data.message || "Failed to fetch bridges");
        }

        setBridges(data.response); // Map to structureNumber
        setLoading(false);
      } catch (err) {
        setError((err as Error).message);
        setLoading(false);
      }
    };

    fetchBridges();
  }, []);
  console.log(bridges)

  return (
    <div className="h-full p-4 overflow-y-auto bg-gray-100">
      <h2 className="text-xl font-bold mb-4">Bridge List</h2>
      {loading && <p>Loading bridges...</p>}
      {error && <p className="text-red-500">Error: {error}</p>}
      {!loading && !error && (
        <ul>
          {bridges.map((bridge: any, index: number) => (
            <li
              key={index}
              className="p-2 mb-2 bg-white hover:bg-gray-200 rounded-md cursor-pointer"
            >
              <h3 className="font-medium">{bridge}</h3>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default Sidebar;